jo.\n                    </p></div> <div data-aos=\"fade-up\" class=\"content mb-20\"><p>\n                      Melalui hal tersebut, bemunculan banyak keluhan umat\n                      Katolik yang menyekolahkan anaknya di sekolah swasta\n                      non-Katolik. Oleh karena itu Romo dan beberapa umat\n                      Katolik di Sidoarjo mencetuskan ide untuk mulai membuka\n                      TK berlandaskan keagamaan Katolik. Maka mulai dilakukan\n                      proses rekrutmen guru TKK dengan syarat utama untuk\n                      mendirikan TKK adalah mencari tenaga pendidik harus\n                      memiliki ijazah TKK. Setelah mendapatkan guru yang\n                      memiliki potensi, akhirnya langsung bergerak menyusun\n                      program persiapan untuk membuka TKK. Setelah persiapan\n                      selesai, maka TKK St. Maria Sidoarjo mulai beroperasi\n                      tepatnya pada tanggal 16 Juli 1989.\n                    </p></div> <div data-aos=\"fade-up\" class=\"content mb-40\"><p>\n                      Untuk pengembangan sekolah berikutnya TKK St. Maria\n                      Sidoarjo mengadakan rapat wali murid di awal tahun dan\n                      akhir tahun kegiatan ini bertujuan untuk peningkatan\n                      mutu sekolah. Kemudian dalam rangka rangka meningkatkan\n                      jumlah murid setiap tahunnya, TKK St. Maria Sidoarjo\n                      membuat gebrakan baru untuk mempromosikan TKK melalui\n                      pembuatan spanduk, brosur, pentas seni dan berbagai\n                      macam kegiatan lain.\n                    </p></div></div></div></div></div></div></div> <div class=\"row\"><div class=\"col-lg-12\"><div data-aos=\"fade-up\" class=\"custom-column-thumbnail mt-60\"><img src=\"/images/project/details-4.png\" alt=\"Agency\" class=\"w-100\"></div></div></div> <div class=\"row mt-60\"><div class=\"col-lg-4 col-md-12 col-12\"><div da